@import "styles/mixins";
@import "styles/variables/index";

.wrapper {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 34.1rem;
    height: 61rem;
    background-image: url("../../../assets/images/fund_details_shape.png");
    background-size: cover;
  }
}

.content {
  @include fontRegular(color(darkGray));

  margin-top: 3rem;
  width: 70rem;

  p {
    margin: 0;

    &:not(:first-child) {
      margin-top: 2.4rem;
    }
  }
}