@import "styles/variables/index";
@import "styles/mixins";

.wrapper {
  padding: 5.6rem 0;
  background-image: url("../../../assets/images/application___bg--web.png");
  background-size: cover;
}

.container {
  padding: 0 11rem;
}

.heading {
  text-align: center;
}

.form {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  grid-gap: 4rem 3rem;
  margin-top: 4rem;
}

.input {
  @include input;
}

.button {
  @include button;

  margin: 4.8rem auto 0 auto;
  width: 41rem;
  height: 7rem;
}