@import "styles/variables/index";
@import "styles/mixins";

.wrapper {
  position: relative;
  padding-bottom: 20rem;
}

.subheading,
.description,
.list {
  width: 42rem;
}

.subheading {
  margin-top: 4rem;
}

.description,
.item {
  @include fontRegular(color(darkGray));
}

.description {
  margin-top: 2.4rem;
}

.list {
  @include resetList;

  margin-top: 2.4rem;
}

.item {
  &:not(:first-child) {
    margin-top: 1.6rem;
  }
}

.image1 {
  position: absolute;
  top: 15rem;
  right: 43rem;
  width: 40.7rem;
  height: 27rem;
  object-fit: cover;
}

.image2 {
  position: absolute;
  top: 32rem;
  right: 0;
  width: 55rem;
  height: 40rem;
  object-fit: cover;
  z-index: 1;
}