@import "styles/variables/index";
@import "styles/mixins";

.wrapper {
  padding-top: 4rem;
  padding-bottom: 3.3rem;
  background-color: color(black);
}

.container {
  display: grid;
  grid-template-columns: 1fr 51.6rem;
  grid-column-gap: 17rem;
}

.address {
  font-style: normal;
}

.heading {
  color: color(white);
}

.list {
  @include resetList;

  display: grid;
  grid-auto-columns: 100%;
  grid-row-gap: 4rem;
  margin-top: 2.4rem;
}

.item {
  display: grid;
  grid-template-columns: 20rem 1fr;
  grid-column-gap: 8rem;
}

.name {
  @include fontRegular;
}

.contacts {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.link {
  @include fontRegular;

  &:not(:first-child) {
    margin-top: 1.6rem;
  }
}

.email {
  text-transform: uppercase;
}

.download {
  @include fontRegular;

  align-self: flex-start;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 4rem;
  padding: 3.4rem;
  text-transform: uppercase;
  border: 1px solid white;
  border-image: linear-gradient(94.98deg, rgba(0, 92, 255, 0.9) 1.39%, rgba(255, 255, 0, 0.3) 97.12%) 1;
}

.icon {
  margin-right: 1.6rem;
  width: 4rem;
  fill: color(white);
}