@import "styles/mixins";
@import "styles/variables/index";

.wrapper {
  position: relative;
  padding-bottom: 18rem;
  overflow: hidden;

  &::before {
    content: "";
    position: absolute;
    top: 1rem;
    left: -3rem;
    width: 33.6rem;
    height: 55.1rem;
    background-image: url("../../../assets/images/fund_shape.png");
    background-size: cover;
  }
}

.frame {
  position: relative;
  padding-top: 10rem;
  padding-right: 56.6rem;
}

.description,
.label {
  @include fontRegular(color(darkGray), 400);
}

.description {
  margin: 3.6rem 0 0 0;
  padding-right: 15rem;
}

.label {
  position: relative;
  display: inline-block;
  margin-top: 7rem;
  margin-right: 20rem;

  &::before {
    content: "";
    position: absolute;
    top: calc(50% - 2rem / 2);
    right: -1rem;
    width: 4rem;
    height: 2rem;
    background-image: url("../../../assets/icons/icon-arrow.svg");
    background-size: cover;
  }
}

.map {
  position: absolute;
  top: 0;
  right: -5rem;
}