@import "styles/mixins";

.wrapper {
  padding-top: 15rem;
  padding-bottom: 20rem;
}

.list {
  @include resetList;

  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  grid-gap: 6rem;
  margin-top: 5.6rem;
}

.button {
  @include resetButton;

  display: block;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
}

.photo {
  width: 100%;
  height: 26.6rem;
  object-fit: cover;
}

.name {
  margin-top: 2.4rem;
}

.rank {
  @include fontRegular(color(darkGray));

  display: inline-block;
  margin-top: 0.8rem;
}