@import "styles/variables/index";
@import "styles/mixins";

.container {
  display: grid;
  grid-template-columns: 1fr 36rem;
  grid-column-gap: 11.4rem;
}

.heading {
  text-align: right;
}

.description {
  @include fontRegular(color(darkGray));

  margin: 0;
}

.slides {
  @include resetList;

  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-rows: 27rem;
  grid-gap: 2.6rem;
  margin-top: 9rem;
}

.item {
  border-radius: 0.4rem;
  overflow: hidden;
  height: 100%;
}

.image {
  width: 100%;
  height: 100%;
}